<script>
import { defineComponent, h, ref  } from "vue";
import { mixinBlock } from "../../mixins";
import { addMixinPrefix } from "../../util/helpers";

export default defineComponent({
    name: "GroupContainer",
    mixins: [...addMixinPrefix([mixinBlock], "group-container")],
    setup(props, { slots }) {

        const defaultContent = () => {
            let childrens = []

            // Filter 
            const defaultSlot = slots.default && slots.default()
            for( let childContent of defaultSlot ){
                if(typeof childContent.type === 'symbol') continue
                // Get child class
                let childClasses = childContent.props ?. class || ''
                // Empty child class
                const isHavingPropertyClass = childContent.props ?. class
                if( isHavingPropertyClass ) childContent.props.class = ''
                // Extend child class to parent (group__item)
                childrens.push(h('div', { class: ['group__item'] }, h(childContent, {
                  class: childClasses
                })))
            }

            return childrens
        }

        return {
            defaultContent
        }
    },
    

    render() {
        return h('div', {
            class: 'group-container'
        }, this.defaultContent())

    }
});
</script>